import React, { useState, useEffect } from 'react'
import axios from 'axios'
import phoneBookService from '../services/phonesbooks'

const Persons = ({ person, handleDeletion }) => {
    return (
        <li>{person.name}: {person.number}

            <button onClick={handleDeletion}> Delete</button>
        </li>
    )
}

const Phonebook = () => {
    const [persons, setPersons] = useState([
        { name: 'Arto Hellas', number: '040-123456' },
        { name: 'Ada Lovelace', number: '39-44-5323523' },
        { name: 'Dan Abramov', number: '12-43-234345' },
        { name: 'Mary Poppendieck', number: '39-23-6423122' }
    ])
    const [newName, setNewName] = useState('')
    const [newPhone, setNewPhone] = useState('')
    const [newSearch, setSearch] = useState('')
    const [notificationMessage, setNotificationMessage] = useState('')
    const [notificationType, setNotificationType] = useState('')

    useEffect(() => {
        phoneBookService
            .getAll()
            .then(initialPersons => {
                console.log('promise fulfilled')
                setPersons(initialPersons)
            })
    }, [])

    const handlePersonNameChange = (event) => {
        console.log(event.target.value)
        setNewName(event.target.value)
    }

    const handlePersonPhoneChange = (event) => {
        setNewPhone(event.target.value)
    }

    const handleFilter = (event) => {
        setSearch(event.target.value)
        console.log(newSearch)
    }

    const contactsToShow = newSearch === '' ? persons : persons.filter(p => p.name.includes(newSearch))

    const addPerson = (event) => {
        event.preventDefault()
        console.log("button clicked", event.target)

        const newObj = {
            name: newName,
            number: newPhone,
        }

        const personExists = persons.find(p => p.name === newObj.name)

        console.log(personExists)

        if (personExists == null) {
            console.log("it gets in")
            phoneBookService
                .create(newObj)
                .then(returnedPerson => {
                    setNotificationMessage("Added " + returnedPerson.name)
                    setNotificationType("success")

                    setTimeout(() => {
                        setNotificationMessage("")
                        setNotificationType("")
                    }, 5000)

                    setPersons(persons.concat(returnedPerson))
                    setNewName('')
                    setNewPhone('')
                })
        }
        else if (personExists != null) {
            const confirmation = window.confirm(`should update person ${personExists.name} `)

            if (confirmation === true)
                phoneBookService
                    .update(personExists.id, newObj)
                    .then(returnedPerson => {
                        setPersons(persons.map(person => person.id !== personExists.id ? person : returnedPerson))
                        setNewName('')
                        setNewPhone('')
                    }
                    )
        }
    }


    const deletePerson = person => {

        const result = window.confirm(`Do you want to delete ${person.name} ?`);

        console.log(result)


        if (result === true)
            phoneBookService
                .remove(person.id)
                .then(response => {
                    console.log('success!')
                    setPersons(persons.filter(p => p.id !== person.id))
                })
                .catch(error => {
                    console.log('fail')

                    setNotificationMessage("Some error happened, see message " + error)
                    setNotificationType("error")

                    setTimeout(() => {
                        setNotificationMessage("")
                        setNotificationType("")
                    }, 5000)
                })
    }

    const Notification = ({ message, type }) => {
        if (message === null) {
            return null
        }
        return (
            <div className={type}>
                {message}
            </div>
        )
    }


    return (
        <div>
            <h3>Filter</h3>
            Filter:
            <input value={newSearch}
                onChange={handleFilter} />

            <h2>Phonebook</h2>
            <Notification type={notificationType} message={notificationMessage} />
            <form onSubmit={addPerson}>
                <div>
                    name:
                <input value={newName}
                        onChange={handlePersonNameChange} />
                    number:
                 <input value={newPhone}
                        onChange={handlePersonPhoneChange} />
                </div>
                <div>
                    <button type="submit">add</button>
                </div>
            </form>
            <h2>Numbers</h2>
            <div>
                <ul>
                    {contactsToShow.map(p =>
                        <Persons person={p} handleDeletion={() => deletePerson(p)} />
                    )}
                </ul>
            </div>
        </div>
    )
}

export default Phonebook